

import {SpecailPicUrl,SpecalilArticleUrl} from '@/api/api'
export default{
data(){
return{
page:1,//页数
BannerList:[],
ArticleList:[],
StartPageY:0,
TypeId:0,
HasData:true
}
},
components:{


},
created(){
this.GetSwiperBanner()

},
mounted(){

},
methods:{
InitSwiper(){
let _this = this
var swiper = new Swiper('.swiper-container', {
slidesPerView: 'auto',
centeredSlides: true,
spaceBetween: 10,
pagination: {
el: '.swiper-pagination',
clickable: true,
},
on:{
slideChange(){
_this.ArticleTypeChange(this.activeIndex)
}
}
});
},
ArticleTypeChange(index){
console.log(index)
this.HasData=true
this.TypeId=this.BannerList[index].id
this.ArticleList=[]
this.page=1
this.GetArticleInfor()
},
GetSwiperBanner(){
this.$http({
method:'GET',
url:SpecailPicUrl
}).then((res)=>{
if (res.data.code===0) {
this.BannerList=res.data.data.data
this.TypeId=this.BannerList[0].id
this.GetArticleInfor()
}
this.$nextTick(()=>{
this.InitSwiper()
})

})
},
GetArticleInfor(){
this.$http({
method:'GET',
url:SpecalilArticleUrl,
params:{p:this.page,id:this.TypeId}
}).then((res)=>{
if (res.data.code===0) {
if (!res.data.data.data.length) {
this.HasData=false
return
}
this.ArticleList=this.ArticleList.concat(res.data.data.data)
}
})
},
MoveStart(e){
this.StartPageY=e.touches[0].pageY
},
MoveEnd(e){
let distance=Math.abs(this.StartPageY-e.changedTouches[0].pageY)
if (distance>100) {
this.page++
this.GetArticleInfor()
}
},
LinkotherSection(e){

let value = e.target.getAttribute("data-index")
console.log(value)
this.$router.push({
path:'/',
query:{section:value}
})
}
}
}



